.top-panel{
	padding: 15px 0;
	background: #e0e0e0;
	font-size: 16px;

	&__basket{
		margin-right: 50px;
	}
}
.top-link{
	display: inline-block;
	vertical-align: middle;
	font-family: $medium;
	color: $blue;

	+ .top-link{
		margin-left: 20px;
	}
}
.header{
	padding: 30px 0;
}
.search{
	width: 545px;
	position: relative;

	&__field{
		display: block;
		width: 100%;
		background: #fff;
		height: 40px;
		border: 1px solid #d0d0d0;
		border-radius: 7px;
		margin-top: 25px;
		padding-left: 20px;
		padding-right: 40px;
		font-size: 16px;
		font-family: $regular;
		outline: 0;
	}
	&__btn{
		position: absolute;
		z-index: 2;
		width: 40px;
		height: 100%;
		background: $blue;
		right: 0;
		top: 0;
		border: 0;
		outline: 0;
		cursor: pointer;
		border-radius: 0 7px 7px 0;

		&:after{
			content: "";
			position: absolute;
			left: 50%;
			top: 50%;
			width: 21px;
			height: 22px;
			z-index: 2;
			transform: translate(-50%, -50%);
			background: url(../images/svg/search.svg)no-repeat;
			background-size: contain;
		}
	}
}
.h-info{
	line-height: 1.3;

	&__tel{
		font-size: 28px;
		font-family: $medium;
	}
}

.user-icon{
	margin-right: 8px;
	width: 22px;
	height: 26px;
	background: url(../images/svg/profile.svg)no-repeat;
	background-size: contain;
}
.user-plus-icon{
	margin-right: 8px;
	width: 24px;
	height: 28px;
	background: url(../images/svg/add.svg)no-repeat;
	background-size: contain;
}
.delivery-icon{
	width: 30px;
	height: 22px;
	margin-right: 8px;
	background: url(../images/svg/delivery_truck.svg)no-repeat;
	background-size: contain;
}
.cart-icon{
	width: 45px;
	height: 40px;
	position: relative;
	margin-right: 15px;
	background: url(../images/svg/supermarket.svg)no-repeat;
	background-size: contain;
}
.cart-count{
	position: absolute;
	z-index: 2;
	right: -5px;
	top: 0;
	border-radius: 50%;
	width: 20px;
	height: 20px;
	background: #b3b3b3;
	text-align: center;
	line-height: 20px;
	font-size: 12px;
}
.hamburger-box {
  position: relative;
  display: inline-block;
  width: 25px;
  height: 19px;
  cursor: pointer;
  user-select: none;

  &.is-active{
  	.hamburger-inner{
  		transform: translate3d(0,9px,0) rotate(45deg);
			
			&:before{
				transform: rotate(-45deg) translate3d(-5.71429px,-6px,0);
    		opacity: 0;
			}
  		&:after{
  			transform: translate3d(0,-16px,0) rotate(-90deg);
  		}
  	}
  }
}
.hamburger-inner {
  top: 50%;
  display: block;
  margin-top: -2px;
  position: absolute;
  width: 25px;
  height: 2px;
  transition-timing-function: ease;
  transition-duration: .15s;
  transition-property: transform;
  top: 2px;
  background-color: #1e88e5;

  &:before{
  	content: "";
  	position: absolute;
    width: 25px;
    height: 2px;
    transition-timing-function: ease;
    transition-duration: .15s;
    transition-property: transform;
    background-color: #1e88e5;
    top: -10px;
    top: 8px;
    transition-timing-function: ease;
    transition-duration: .15s;
    transition-property: transform,opacity;
  }
  &:after{
  	content: "";
  	position: absolute;
    width: 25px;
    height: 2px;
    transition-timing-function: ease;
    transition-duration: .15s;
    transition-property: transform;
    background-color: #1e88e5;
    bottom: -10px;
    top: 16px;
  }
}
.mobile-panel{
	display: none;
	width: 100%;
	left: 0;
	top: 70px;
	position: absolute;
	z-index: 10;
	background: #e0e0e0;
	padding: 15px 0;
	border-top: 1px solid #1578e0;

	&__menu{
		font-size: 18px;
		font-family: $medium;

		li{
			+ li{
				margin-top: 10px;
			}
		}

		a{
			color: #3e3e3e;
		}
	}
}
.search-result{
	display: none;
	margin-top: 20px;
	position: absolute;
	z-index: 5;
	top: 100%;
	width: 610px;
	background: #fff;
	padding-left: 20px;
	padding-right: 20px;
	border: 1px solid #1578e0;
	border-radius: 8px;

	&:before{
		content: "";
		display: block;
		width: 11px;
		height: 12px;
		position: absolute;
		z-index: 2;
		left: 35px;
		top: -11px;
		background: url(../images/search-arrow.png)no-repeat;
	}

	&__footer{
		padding-bottom: 20px;
		text-align: right;
	}

	&__btn{

		.btn{
			font-size: 11.5px;
			font-family: $medium;
			height: 30px;
			line-height: 26px;
		}
	}

	&__item{
		padding-top: 25px;
		padding-bottom: 25px;

		+ .search-result__item{
			border-top: 1px solid #d0d0d0;
		}
	}
	&__img{
		width: 55px;
	}
	&__price{
		width: 60px;
		
		.product__price{
			font-size: 14px;
			margin-bottom: 0;

			span{
				display: block;
				font-size: 18px;
			}
		}
	}
	&__info{
		width: 155px;

		.product__name{
			font-size: 16px;
			height: auto;
		}
		.product__articul{
			font-size: 12px;
			margin-bottom: 5px;
		}
	}
}